import React from 'react';
import ReactDOM from 'react-dom';
import './css/index.css';
import 'bootstrap/dist/css/bootstrap.css';
import {BrowserRouter as Router, Route } from 'react-router-dom';
import Home from './Home';
import Digital from './Digital';
import Traditional from './Traditional';
import Photography from './Photography';


ReactDOM.render(
  <Router>
	  <main>
		  <Route exact path={process.env.PUBLIC_URL + '/'} component={Home}/>
      <Route exact path={process.env.PUBLIC_URL  + '/traditional'} component={Traditional}/>
      <Route exact path={process.env.PUBLIC_URL  + '/digital'} component={Digital}/>
      <Route exact path={process.env.PUBLIC_URL  + '/photography'} component={Photography}/>
	  </main>
	</Router>,
  document.getElementById('root')
);
